
import dayjs from "dayjs";
import { mapState } from "vuex";
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'

import { backMixin } from "@/common/js/picBack";
export default {
mixins: [backMixin],
components: {
Flexbox,
FlexboxItem,
XHeader,
Actionsheet,
ButtonTab,
ButtonTabItem,
Toast,
Loading,
Previewer
},
directives: {
TransferDom
},
data() {
return {
showMenus: false,
isShow: false,
menus: {
menu1: "修改",
menu2: "删除"
},
menuDelete: {
delete: '<span style="color:red">删除</span>'
},
formData: {},
CostInfoDate: "",
CostInfoType: "",
fId: "",
menuHide: false,
ifChange: false,
options: {
saveImg(el) {}
}
};
},
mounted() {
this.fId = this.$route.params.id;
this.getData();
this.$bus.on("onSave", () => {
this.getData();
this.$bus.emit("refrechCostList", true);
});
},
beforeDestroy() {
this.$bus.off("onSave");
},
methods: {

goto() {
let path = "/cost/newCost";
if (this.CostInfoType == "支出") {
path = "/reimbursement";
}
if (this.$route.path.lastIndexOf("client") >= 0) {
path = "/client/clientDetails/newCost";
if (this.CostInfoType == "支出") {
path = "/client/clientDetails/reimbursement";
}
}
let id = this.fId;
this.$router.push({ path, query: { id } });
},








getData() {
this.$vux.loading.show({
text: "加载中..."
});
this.getAxios(`/Cost/GetCrmCostRecordByID/${this.fId}`).then(res => {
this.formData = { ...res.Data };
if (this.formData.CostType == 1) {
this.CostInfoType = "收入";
}
if (this.formData.CostType == 2) {
this.CostInfoType = "支出";
}
if (this.formData.CostDate) {
this.CostInfoDate = dayjs(this.formData.CostDate).format(
"YYYY-MM-DD"
);
}
this.formData.ImgAttach = this.formData.ImgAttach || [];
this.formData.ImgAttach.forEach(item => {
item.src = `${api50}/api/v1/File/DownLoadPic?filePath=${
item.FilePath
}`;
});
this.$vux.loading.hide();
});
},
onDelete() {
let id = this.formData.ID;
let _this = this;
this.$vux.confirm.show({
content: "确定删除该条数据？",
onConfirm() {
_this.ifChange = true;
_this.postAxios(`/Cost/DeleteCrmCostRecord/${id}`).then(res => {
_this.$vux.toast.show({
text: "删除成功",
type: "success"
});
setTimeout(() => {
_this.$vux.toast.hide();
_this.$bus.emit("refrechCostList", true);
_this.$router.back();
}, 500);
});
}
});
},
isDel(val) {
return (
this.isAdmin ||
(val
? new Date().getTime() - new Date(val.replace(/-/g, "/")).getTime() <=
86400000
: "")
);
}
},
watch: {
getorderid(curval, oldval) {

console.log(`最新值${curval}--旧值${oldval}`);
}
},
filters: {
picUrl(path, w = 0, h = 0) {
return `${api50}/api/v1/File/DownLoadPic?filePath=${path}&w=${w}&h=${h}`;
}
},
computed: {
...mapState(["isMyClient", "isAdmin"]),
getorderid() {
return this.$store.state.customerName;
}
}









};



